export const trinketDescriptionData = {
  138: "Przy użyciu aktywnego przedmiotu przelosowuje go w inny.\nNowy przedmiot będzie w całkowicie nienaładowany.\nNowy przedmiot nie liczy się do żadnej transformacji, nawet jeśli Isaac odłoży go i podniesie ponownie.\nPrzedmiot zostanie przelosowany dopiero, gdy jego ładunek zostanie zużyty, np. w przypadku Spin to Win cały ładunek musi zostać wyczerpany. 'M nie działa na przedmioty jednorazowego użytku (oprócz Sacrifical Altar, który może zostać użyty bez bycia zużytym, gdy Isaac nie ma żadnych chowańców).\n'M nie może przelosować przedmiotu w Broken Shovel, Mom's Shovel, Bag of Crafting, Hold, Sumptorium, Flip ani Recall.",
  3: "Gdy po przejściu pokoju aktywny przedmiot Isaaca zyskuje przedostatni ładunek, jest zamiast tego w pełni naładowany. Nie dotyczy to ładowania przedmiotu z innych źródeł (np. przez Habit)\nNie ma wpływu na przedmiot o 1 ładunku lub te, które ładują się automatycznie.",
  45: "Daje 10% szansy, że nagroda po przejściu pokoju zostanie zmieniona w kartę.\nDaje szansę na dodanie karty do znajdziek wyrzuconych przez skrzynki, Tinted Rocki, Slot Machine'y i Fortune Telling Machine'y.",
  184: "Sklepy odwiedzone po podniesieniu trinketu będą sprzedawały chowańce zamiast innych przedmiotów. Jeśli Isaac trzyma Adoption Papers, chowańce będzie przecenione na 10 monet; w przeciwnym wypadku będą kosztowały 15 monet.\nW Greed Modzie, tylko przedmiot z puli Sklepu zostanie zastąpiony przez chowańca.",
  186: "Przyzywa szarańczę, która lata w kierunku, w którym Isaac strzela, i zadaje obrażenia kontaktowe równe 150% Obrażeń Isaaca.",
  140: "Przy podniesieniu czerwonych serc z ziemi istnieje 50%, że zmienią się one w Niebieskie Pająki i wytworzą bąka. Isaac nie otrzymuje wtedy zdrowia.\nJeśli Isaac ma pełne zdrowie lub nie ma w ogóle Czerwonych Serc, efekt zawsze zostanie wywołany.\n1/2 Czerwonego Serca wyrzuca 1-3 Niebieskie Pająki, całe Czerwone Serce 3-5 pająków, a Podwójne Czerwone Serce 6-8 pająków. Efekt nie działa na żadne inne serca.",
  162: "Daje Isaacowi szansę na wizualne zmienienie się w Azazela i zyskanie krótkiego Brimstone'a oraz lotu po przejściu pokoju. Efekt trwa do przejścia i wyjścia z następnego pokoju.",
  135: "Ma szansę na podpalenie przeciwników przy wejściu do nowego pokoju. Podpalenie zadaje Obrażenia Isaaca na tick, do 5 razy.\nSzansa jest zależna od Szczęścia i wynosi 100% przy Szczęściu 40.",
  48: "Przy otrzymaniu obrażeń przez Isaaca, ma szansę na zadanie 80 Obrażeń wszystkim przeciwnikom w pokoju. Szansa jest zależna od Szczęścia i maksymlanie wynosi ok. 50% przy wysokim Szczęściu.\nDodaje +40 Obrażeń do podobnych efektów, np. z Black Heartów lub The Necronomiconu.",
  127: "Daje chowańcom Isaaca efekt samonaprowadzających łez, wliczając płód Tainted Lilith.",
  105: "Przy otrzymaniu obrażeń przez Isaaca istnieje 5% szansy na to, że trinket się zniszczy i wygeneruje Lunch.",
  182: "Przy wejściu do Angel Roomu generuje 5 orbitali-ogników.\nWpłacanie żebraczkom ma szansę na wygenerowanie orbitala-ognika.",
  142: "Przy przejściu na nowe piętro wygeneruje 4 orbitale-ogniki, takie same, jak domyślne ogniki Book of Virtues.",
  18: "Zwiększa szansę na pojawienie się Eternal Heartów.",
  80: "Daje Isaacowi bonus do Obrażeń +0,5 za każdy z następujących przedmiotów/trinketów, który posiada: Abaddon, Black Candle, Black Lipstick, Ceremonial Robes, Daemon's Tail, False PHD, Goat Head, Match Book, Missing Page 2 lub Safety Pin.",
  17: "Podwaja szansę na pojawienie się Black Heartów.",
  73: "Przy wybuchu bomby Isaaca mają szansę na wyrzucenie znajdźki-bomby.\nNie ma efektu na bomby z Dr. Fetusa.",
  131: "Daje szansę na wygenerowanie 1/2 Soul Hearta, gdy Isaac podnosi monetę. Szansa jest zależna od wartości monety:\n* Penny - 16,6%\n*Double Penny - 30,5%\n* Nickel - 59,7%\n* Dime - 83,3%",
  81: "Podwaja klatki nietykalności, jakie Isaac dostaje po otrzymaniu obrażeń.",
  77: "Zwiększa odrzut łez Isaaca.",
  49: "Daje 25% szansy na wygenerowanie 1/2 Czerwonego Serca, gdy Isaac podnosi monetę.\nMa szansę wypaść ze Special Shopkeepera.",
  57: "Przyzywa chowańca, który lata po pokoju po przekątnej i co 2/3 sekundy strzela spektralnymi i samonaprowadzającymi łzami, które zadają 3,5 Obrażeń.\nBox of Friends nie podwaja tego chowańca.",
  150: "Wejście do pokoju, który wymaga klucza, nie zużyje klucza, ale za to przeniesie Isaaca do długiego niebieskiego pokoju przypominającego piętro ???. Pokój ten może zawierać Blue Gapery, Blue Boile i/lub Blue Conjoined Fatty.\nWyjście z pokoju prowadzi do pokoju, do którego Isaac chciał wejść. Niebieski pokój nie może zostać ponownie odwiedzony.\nEfekt działa również na drzwi, które wymagają 2 kluczy, drzwi, które wymagają monety do wejścia, drzwi do przejścia do Downpouru/Drossu (w ich przypadku wygenerowany pokój nie będzie miał wyjścia, ale po wyjściu przez drzwi, przez które Isaac wszedł, drzwi będą otwarte) oraz drzwi do Husha.",
  71: "Bomby Isaaca zostawiają za sobą zielony śluz, który zadaje 23,3 Obrażeń na tick.",
  144: "Łzy Isaaca zakręcają o 90 stopni i samonaprowadzają się na przeciwników, których by normalnie nie trafiły.",
  28: "Daje Isaacowi 22,22% szansy na odrodzenie się jako ???. Efekt może zajść wiele razy podczas rozgrywki.\nBroken Ankh jest dziewiąty w kolejności aktywacji przedmiotów/trinketów odradzających, przed Judas' Shadow, Missing Poster i Birthright.\nTainted ??? zostanie odrodzony jako on sam, a nie ???.",
  187: "Daje 33% szansy na dodanie niewidocznego przedmiotu do Treasure Roomów. Na alternatywnych piętrach zamiast tego ujawnia dodatkowe przedmioty.\nPo podniesieniu jednego przedmiotu, drugi znika.",
  6: "Przyciąga monety w stronę Isaaca.",
  136: "Umożliwia Isaacowi na otwarcie drzwi, które normalnie wymagają klucza, monety lub serca (wliczając drzwi do przejścia do Mausoleum/Gehenny oraz A Strange Door), oraz skrzynek (wliczając Mimic Chesty i Spiked Chesty), za pomocą bomby.\nEfekt nie działa na drzwi do Mega Szatana, drzwi do Mom's Heart w Mausoleum II/Gehennie II, Haunted Chesty oraz Mom's Chest.\nDrzwi, które wymagają 2 kluczy lub 2 serc do otwarcia, mogą zostać otwarte tylko 1 bombą. Mega Chesty wymagają za to wielu bomb.",
  4: "Teleportuje Isaaca do losowego pokoju przy użyciu przedmiotu aktywnego.",
  132: "Przy wejściu do nowego pokoju Isaac ma szansę (niezależną od Szczęścia) na otrzymanie efektu losowej strzykawki na czas trwania obecnego pokoju.\nEfekt Experimental Treatment da Isaacowi 3 losowe bonusy lub minusy do statystyki, nie wliczając Zdrowia.",
  90: "Kupy przy zniszczeniu wybuchają, zadając 100 Obrażeń.\nEfekt nie działa na Czerwone Kupy.",
  50: "Daje 25% szansy na wygenerowanie bomby, gdy Isaac podnosi monetę.\nMa szansę wypaść z TNT lub Special Shopkeeperów.",
  24: "Przy podniesieniu monety, Isaac puszcza bąka, odpychając i zatruwając pobliskich przeciwników.\nZwiększa szansę na wypadnięcie monet z kup.",
  59: "Przy przejściu na nowe piętro, ma szansę na wywołanie efektu The Compass.\nSzansa jest zależna od Szczęścia i wynosi 100% przy Szczęściu 3.",
  14: "Daje Isaacowi odporność na śluz oraz kolce na ziemi.\nEfekt nie działa na spowolnienie z Pajęczyn ani na kolce na przycisku zatrzymującym fale w Greed Modzie.",
  39: "-2 Opóźnienia Łez.",
  8: "Daje szansę na wywołanie efektu The Gamekid przy otrzymaniu obrażeń przez Isaaca.\nSzansa jest zależna od Szczęścia i wynosi 100% przy Szczęściu 38.",
  147: "Daje szansę na dodanie 1 ładunku do przedmiotu aktywnego, gdy Isaac podnosi monetę.\nSzansa jest równa 1/6 wartości monety.",
  130: "Daje Isaacowi szansę na wystrzelenie spowalniającej łzy.\nSzansa jest zależna od Szczęścia i wynosi 100% przy Szczęściu 18.",
  34: "Daje 10% szansy, że nagroda po przejściu pokoju zostanie zmieniona w serce.\nDaje szansę na dodanie serca do znajdziek wyrzuconych przez skrzynki, Tinted Rocki, Slot Machine'y i Fortune Telling Machine'y.\nTrinket startowy Samsona po przejściu wyzwania #34: Ultra Hard.",
  89: "Chowańce Isaaca trzymają się bliżej Isaaca.",
  52: "Za każdym razem, gdy Isaac podnosi monetę, ma 50% szansy na otrzymanie 1 dodatkowego centa.",
  92: "Zwiększa Szybkość, Zasięg, Szybkość Strzałów, Obrażenia, Łzy i Szczęście, jeśli są one powyżej ich bazowych wartości. Bonusy do tych statystyk wynoszą ok. 20% ich wartości ponad wartość bazową.",
  67: "Przy otrzymaniu obrażeń przez Isaaca ma szansę wywołać efekt The D6, D8, D10, D12 lub D20.",
  185: "Ma szansę na wygenerowanie losowej szarańczy, gdy Isaac zabije przeciwnika.",
  107: "Gdy Isaac otrzyma obrażenia, otrzyma je najpierw w Czerwone Serca, a nie Soul Hearty/Black Hearty/Rotten Hearty.\nBone Hearty zostaną najpierw opróżnione z Czerwonych Serc, ale nie zostaną zniszczone, jeśli Isaac ma czerwone serca.",
  170: "Daje szansę na otwarcie Czerwonego Pokoju po przejściu pokoju.\nSzansa zależy od liczby posiadanych Crystal Key i tego, czy pokój, który Isaac przeszedł, jest normalny czy sam jest czerwony:\n* 1: normalny - 33,3%, czerwony - 12,5%\n* 2: normalny - 50%, czerwony - 16,6%\n* 3: normalny - 100%, czerwony - 25%\n* 4: normalny - 100%, czerwony - 50%\n* 5: normalny - 100%, czerwony - 100%",
  172: "Gdy Isaac podniesie monetę, zostanie przeteleportowany do losowego pokoju.",
  43: "Jeśli po otrzymaniu obrażeń Isaac będzie miał 1/2 serca lub mniej (oprócz pustych Bone Heartów), zostanie przeteleportowany do losowego pokoju.\nEfekt nie bierze pod uwagę pustych Bone Heartów, np. gdy Isaac posiada 12 pustych Bone Heartów i otrzyma obrażenia, zostanie przeteleportowany.",
  35: "Isaac otrzymuje bonus +2 Obrażeń, aplikowany po wszystkich obliczeniach obrażeń, ale przed mnożnikami.",
  22: "80% znajdziek-serc jest zastąpione kluczami. Wszystkie pozostałe znajdźki-serca są zastąpione Black Heartami, jeśli to możliwe (jest to niemożliwe, gdy dana mechanika zawsze wyrzuca dany typ serca, np. Tinted Rock, Little C.H.A.D., niebieskie ogniska, Super Secret Roomy z ustalonym typem serce, itp.).",
  146: "Przy podniesieniu trinketu nieodwiedzone Treasure Roomy zostaną zastąpione przez Czerwone Treasure Roomy, które będą oferowały przedmioty z puli Devil Roomu za ich normalny koszt w układzie z Diabłem. Zakup tych przedmiotów wpływa na szansę na Angel Room.\nPodwójne Treasure Roomy będą zawierały 2 przedmioty, z których oba mogą zostać zakupione.\nTrinket wpływa na Treasure Roomy, które nie zostały jeszcze odwiedzone. Wyrzucenie trinketu zmieni je z powrotem w zwykłe Treasure Roomy, jeśli nie zostały odwiedzone.\nPodczas Ascentu, otwarte Treasure Roomy nie mogą zostać zmienione, natomiast nieotwarte i nieodwiedzone tak.\nNie ma efektu w Greed Modzie.",
  154: "Ma 50% szansy na danie Isaacowi przedmiotu-kostki (oprócz Spindown Dice i D Infinity) w miejscu na kartę/pigułkę, gdy wejdzie on do nowego pokoju. Kostka znika, gdy Isaac wyjdzie z pokoju.\nEfekt daje Isaacowi również możliwość trzymania 2 kart/pigułek na dany pokój, więc kostka nie zastąpi jego obecnie trzymanej karty/pigułki.\nTyp kostki jest zależny od liczby pokoi, które Isaac przeszedł.",
  101: "Daje Isaacowi +0,5 Szybkości, +1,5 Zasięgu, +0,5 Łez, +0,3 Szybkości Strzałów, +1,5 Obrażeń i +2 Szczęścia, gdy jego przedmiot aktywny jest w całkowicie nienaładowany.\nEfekt nie działa na przedmioty aktywne bez ładunków ani gdy Isaac nie posiada przedmiotu aktywnego.",
  163: "Po przejściu pokoju generuje przyjacielskiego Dipa.\nZmienia wszystkie Dipy w przyjacielskie Dipy.",
  124: "Drzwi, przez które Isaac wszedł do pokoju, zostają otwarte, pozwalając mu wyjść.\nEfekt nie działa na drzwi zamykane przez inne mechaniki, np. drzwi do Challenge Roomu zamykające się po otwarciu skrzynki/wzięciu przedmiotu, drzwi Boss Roomu z walką z Mamą, drzwi do Boss Rushu oraz drzwi do Mega Szatana.",
  109: "Orbitale, strzelające chowańce oraz chowańce, które podążają za Isaaciem zatrzymują się w stałym miejscu względem Isaaca i poruszają się tylko, gdy on się porusza.",
  79: "Gdy Isaac użyje karty/pigułki/itp., istnieje 25% szansy, że wygenerowana zostanie jej kopia.\nKopia zostanie wygenerowana przed tym, gdy zajdzie efekt karty/pigułki/itp., np. Ace of Diamonds lub Black Rune zaabsorbuje kopię.",
  103: "Gdy Isaac ma równą liczbę monet, bomb i kluczy, wszystkie znajdźki zostaną zmienione w ich podwójną wersję.\nZnajdźki już podwojone przez efekt trinketu nie powrócą do normalnej wersji, gdy liczba monet, bomb i kluczy Isaaca stanie się nierówna.",
  75: "Daje Isaacowi efekt losowego trinketu na czas trwania pokoju.\nDany efekt jest przypisany do danego pokoju, a więc wyjście i ponowne wejście do pokoju nie zmieni efektu.",
  60: "Daje Isaacowi szansę na przyzwanie chowańca podobnego do Dead Bird na czas trwania obecnego pokoju za każdym razem, gdy przeciwnik ginie.\nChowaniec zadaje 2 Obrażeń na tick. Tylko jeden może zostać wygenerowany na pokój.\nSzansa jest zależna od Szczęścia i wynosi 5% przy Szczęściu 0 i 100% przy Szczęściu 8.",
  181: "Przy użyciu przedmiotu aktywnego, aktywuje efekt losowego przedmiotu, którego ładunek wynosi 1 lub 2 pokoje, oprócz Metronome i D Infinity.\nPrzedmioty, które ładują się czasowo lub których ładunek jest nielimitowany, mogą aktywować dodatkowy efekt tylko raz na pokój.",
  125: "Pomiędzy Isaaciem a jego chowańcami będą pojawiały się elektryczne iskry, które zadają 6 Obrażeń.\nIskry będą również się pojawiały między The Soul a ciałem The Forgottena/Tainted Forgottena.\nIskry nie pojawiają się między chowańcami wygenerowanymi przez Quints.",
  69: "Co jakiś czas Isaac zlewa się z otoczeniem, dezorientując wszystkich przeciwników w pokoju.\nUmożliwia otwarcie A Strange Door (chyba że został zaabsorbowany przy pomocy Marbles, Smeltera lub pigułki Gulp!).",
  123: "Anioły będą wyrzucały przedmiot z puli Angel Roomu zamiast kawałków klucza, nawet jeśli oba kawałki zostały już zebrane.\nNie dotyczy to aniołów spotkanych w pokojach w Cathedral ani podczas Boss Rusha (ale aniołów w Sacrifice Roomach i Super Secret Roomach tak).",
  128: "Daje Isaacowi 5% szansy na otrzymanie Bone Hearta przy otrzymaniu obrażeń.",
  29: "Generuje Niebieską Muszkę, gdy Isaac otrzymuje obrażenia.",
  151: "Powoduje, że wszystkie kolce są schowane i nie zadają obrażeń, wliczając kolce w drzwiach do Curse Roomu, kolce w Sacrifice Roomie oraz kolce Spiked Rocków, Spiked Chestów, Mimic Chestów, Poky'ch, Slide'ów i Wall Huggerów. Spikeballe kompletnie znikają.\nEfekt nie działa na Grudge, Ball and Chain, kolczaste kule podczas walki z Singe'em oraz ataki kolcami The Pile i Mamy Gurdy.",
  51: "Daje 25% szansy na wygenerowanie klucza, gdy Isaac podnosi monetę.",
  12: "Łzy Isaaca są 50% szersze i stają się podłużne oraz mają zwiększony odrzut.\nMa małą szansę wypaść z Larry'ego Jr., Scolexa, Hollowa lub Pina.",
  141: "Strzelające chowańce Isaaca otrzymują podwojoną szybkostrzelność, a te, które ładują ataki, mają czas ładowania skrócony o połowę.\nMa małą szansę wypaść z Siren.",
  180: "Przyzywa chowańca, który naśladuje ruchy Isaaca i strzela łzami spektralnymi, które mają statystyki Łez, Zasięgu, Szybkości Strzałów i efekty łez takie same, jak łzy Isaaca, i zadają 0,5x Obrażenia Isaaca.\nChowańca można oddzielić od Isaaca, przytrzymując klawisz zmiany karty/pigułki (domyślnie Ctrl).\nGdy Isaac postawi bombę, chowaniec postawi drugą za darmo.\nChowaniec może naciskać przyciski.\nChowaniec umiera po otrzymaniu obrażeń i powraca na początku następnego piętra.",
  148: "Chowańce, które normalnie podążają za Isaaciem, będą zamiast tego go orbitować.",
  134: "Bąki puszczane przez Isaaca lub przeciwników są znacznie większe.",
  159: "+1 klucz przy pierwszym podniesieniu.\nWszystkie nowe skrzynki (oprócz Old Chestów i Mega Chestów) są zastąpione Golden Chestami.\nGolden Chesty, oprócz normalnych znajdziek, mogą wyrzucić również karty, pigułki oraz trinkety.",
  37: "+0,15 Szybkości i maksymalnej Szybkości.",
  120: "Gdy Isaac pierwszy raz wejdzie do Boss Roomu, jego przedmiot aktywny zostanie w pełni naładowany.\nW Greed Modzie efekt działa tylko w walce z Ultra Greedem/Ultra Greedierem i w Boss Roomie przed nią.\nEfekt nie działa pomiędzy Dogmą a The Beast.",
  168: "Daje Isaacowi 1 Bone Hearta na początku każdego piętra.",
  155: "Powoduje, że Treasure Room i Sklep zostaną wygenerowane w Cathedral.",
  26: "+1,5 Zasięgu.\n+0,4 Łez.\nDaje Isaacowi łzy spektralne.\nŁzy Isaaca poruszają się zygzakiem.",
  188: "Przy wejściu do pokoju istnieje szansa, że przeciwnicy zostaną unieruchomieni. Efekt ten trwa 4 sekundy.\nPrzeciwnicy, którzy zostali unieruchomieni, zostaną zamrożeni przy śmierci, wliczając tych zabitych do 2 sekund po zakończeniu efektu unieruchomienia.\nSzansa na unieruchomieni i liczba unieruchomionych przeciwników zależy od Szczęścia.\nNie działa na fale w Greed Modzie.",
  46: "Daje Isaacowi szansę na uleczenie się o 1/2 Czerwonego Serca przy przejściu pokoju.\nSzansa jest zależna od Szczęścia i wynosi 100% przy Szczęściu 18.",
  54: "Przyzywa chowańca, który podąża za Isaaciem i co 2/3 sekundy strzela przeszywającymi łzami, które zadają 3,5 Obrażeń.",
  129: "Daje Isaacowi szansę na wystrzelenie zęba, który zadaje 3,2x jego Obrażeń.\nSzansa wynosi 1/(10 - Szczęście).",
  56: "Powoduje, że wszystkie przedmioty w Devil Roomach, które kosztują czerwone serca, będą kosztowały 1 Czerwone Serce.\nNie ma efektu na przedmioty kosztujące Soul Hearty.",
  85: "Wpłacenie do Donation Machine ma szansę na uleczenie Isaaca o 1 Czerwone Serce, wygenerowanie żebraczka lub oddanie Isaacowi wpłaconej monety.\nZwiększa szansę na otrzymanie bonusu do Szczęścia przy wpłacaniu do Donation Machine.",
  171: "Daje szansę na to, że przedmioty w Devil Roomach, Black Marketach lub Sklepach z A Pound of Flesh będą kosztowały pieniądze, a nie serca.\nPrzedmioty, które normalnie kosztowałyby 1 serce, będą kosztować 15 monet, a te, które kosztowałyby 2 serca, będą kosztować 30 monet. Przedmioty mogą być czasami przecenione.\nNie ma efektu na The Lost i Tainted Lost.",
  169: "Liczy się jako jeden z przedmiotów potrzebnych do transformacji w Guppy'ego.\nEfekt działa tylko wtedy, gdy Isaac trzyma trinket. Po wyrzuceniu go, jeśli Isaac ma mniej niż 3 przedmioty Guppy'ego, straci on transformację.",
  66: "-0,5 Szybkości Strzałów.\n+2 Wysokości Łez.",
  32: "Gdy Isaac wchodzi do pokoju, ma szansę na otrzymanie efektu jednego z następujących przedmiotów: Blue Cap, God's Flesh, Magic Mushroom, Mini Mush, Odd Mushroom, Odd Mushroom.\nMa szansę wypaść z grzybów.",
  176: "Przyzywa chowańca, który podąża za Isaaciem i strzela łzami, które używają Łzy, Zasięg, Szykbości Strzałów i efekty łez Isaaca, oraz zadają 0,35x jego Obrażeń.\nChowaniec umiera po otrzymaniu obrażeń 3 razy. Odradza się po przejściu do nowego pokoju.\nIsaac może poruszać się niezależnie od chowańca przy przytrzymaniu klawisza zmiany karty/pigułki (domyślnie Ctrl).",
  86: "Generuje 1 lub więcej Niebieskich Muszek z każdej kupy, który Isaac zniszczy.",
  117: "Przy wejściu do pokoju z przeciwnikami, generuje 1-4 białych szarańczy, które zadają 2x Obrażenia Isaaca.\nSzarańcze zostaną wygenerowane również co każdą falę Boss Rushu, ale nie Greed Mode'u.\nMa małą szansę wypaść z Conquesta.",
  116: "Przy wejściu do pokoju z przeciwnikami, generuje 1 czarną szarańczę, która zadaje 2x Obrażenia Isaaca przez 2 ticki.\nSzarańcze zostaną wygenerowane również co każdą falę Boss Rushu, ale nie Greed Mode'u.\nMa małą szansę wypaść z Deatha.",
  115: "Przy wejściu do pokoju z przeciwnikami, generuje 1 żółtą szarańczę, która zadaje 2x Obrażenia Isaaca i spowalnia przeciwników.\nSzarańcze zostaną wygenerowane również co każdą falę Boss Rushu, ale nie Greed Mode'u.\nMa małą szansę wypaść z Famine'a.",
  114: "Przy wejściu do pokoju z przeciwnikami, generuje 1 zieloną szarańczę, która zadaje 2x Obrażenia Isaaca i zatruwa przeciwników. Trucizna zadaje 2x Obrażenia Isaaca przez jakiś czas.\nSzarańcze zostaną wygenerowane również co każdą falę Boss Rushu, ale nie Greed Mode'u.\nMa małą szansę wypaść z Pestilence'a.",
  113: "Przy wejściu do pokoju z przeciwnikami, generuje 1 czerwoną szarańczę, która wybucha przy kontakcie z przeciwnikiem, zadając 60 + 2x Obrażenia Isaaca.\nSzarańcze zostaną wygenerowane również co każdą falę Boss Rushu, ale nie Greed Mode'u.\nMa małą szansę wypaść z Wara.",
  106: "Zwiększa zasięg śluzu generowanego przez Isaaca lub jego chowańce.",
  70: "Co jakiś czas, w pokoju z przeciwnikami, generuje Niebieskiego Pająka.\nPo tym, jak pierwszy pająk zostanie wygenerowany, istnieje 50% szansy, że następny zostanie wygenerowany po 30 sekundach, oraz 100% po 1 minucie.",
  15: "Kamienie i ich warianty mają 33% szansy na wyrzucenie 1 centa.",
  160: "Na początku każdego piętra zostanie wygenerowana sakiewka.",
  42: "+1 Szczęścia.\nDaje 33% szansy na 1 dodatkową znajdźkę przy otwieraniu Golden Chesta lub niszczeniu maszyny lub Tinted Rocka.\nZwiększa szansę na dostanie nagrody po przejściu pokoju (niezależną od Szczęścia).",
  55: "Daje Isaacowi Eternal Hearta na początku każdego piętra.",
  41: "Daje 10% szansy na zmienienie nagrody po przejściu pokoju na bombę.\nDaje szansę na dodanie bomby do znajdziek wyrzuconych przez skrzynki lub niszczenie Tinted Rocków, Slot Machine i Fortune Telling Machine.\nJest to jedyny trinket, który może zastąpić Ticka.\nMa szansę wypaść z TNT i zawsze wypada przy podniesieniu Match Booka.",
  91: "Daje szansę na zamienienie kupy w Black Poop oraz na to, że Black Poop wyrzuci Black Hearta przy zniszczeniu.\nSzansa jest niezależna od Szczęścia.",
  23: "Jeśli Isaac zginie przez kolce w Sacrifice Roomie, gdy będzie trzymał ten trinket, odblokowany zostanie The Lost.\nJeśli The Lost został już odblokowany, jeśli Isaac zginie, trzymając ten trinket, zostanie odrodzony jako The Lost, a trinket zostanie zniszczony. Tainted Lost zostanie odrodzony jako Tainted Lost.\nMissing Poster jest przedostatni w kolejności aktywacji przedmiotów odradzających, przed Birthright.",
  166: "Ma 50% szansy na wywołanie efektu losowego przedmiotu pasywnego z puli obecnego pokoju, na czas trwania obecnego pokoju.\nPrzedmioty, które normalnie wyrzucają lub usuwają znajdźki/zdrowie Isaaca, nie zrobią tego, gdy Modeling Clay wywoła ich efekt.\nChaos wpływa na Modeling Clay, powodując, że będzie on wywoływać efekt przedmiotu z losowej puli.",
  87: "Zużycie klucza na otwarcie skrzynki, drzwi lub Key Blocka leczy Isaaca o 1/2 Czerwonego Serca (a Keepera/Tainted Keepera o 1 monetę).\nZamienia wszystkie połówki czerwonych serc w pełne serca, również te, które leżą już na ziemi.\nEfekt nie zadziała przy otwieraniu skrzynek/drzwi/Key Blocków za pomocą Golden Key lub Paper Clipa, ani przy wpłacaniu do Key Mastera.",
  153: "Ma szansę na wywołanie efektu losowego przedmiotu Mamy (oprócz Mom's Coin Purse i Mom's Purse) w każdym pokoju. Przedmioty o mniejszej jakości mają większy priorytet.\nEfekt przedmiotu nie wygeneruje znajdziek ani nie da Isaacowi zdrowia.",
  38: "Daje szansę, że dowolna znajdźka-serce zostanie zmieniona w Soul Hearta.",
  16: "Co 20 sekund, do pokoju spadnie noga Mamy, zadając 300 Obrażeń i otwierając drzwi/wejścia do Secret Roomów/Stone Chesty. Noga zadaje Isaacowi pełne serce obrażeń, jeśli go trafi.\nNoga nie namierza się ani na Isaaca, ani na przeciwników.",
  20: "Jeśli Isaac 1/2 serca lub mniej po otrzymaniu obrażeń, trinket generuje Black Hearta.\nEfekt ten działa 3 razy, po czym trinket znika.",
  156: "Isaac otrzymuje kontener Czerwonego Serca, gdy trzyma ten trinket. Serce jest wypełnione pierwszy raz, gdy trinket jest podniesiony.\nJeśli jedynym Czerwonym Sercem, jakie posiada Isaac, jest serce z Mother's Kiss, trinket zostanie zniszczony, jeśli to serce zostanie oddane (np. w Devil Roomie).\nJeśli Isaac ma tylko jedno Czerwone Serce i wyrzuci trinket, natychmiast zginie.\nDodatkowy kontener z tego trinketu może przekroczyć limit monet Keepera i Tainted Keepera.",
  137: "Na początku piętra generuje znajdźki zostawione na poprzednim piętrze, maksymalnie 4.",
  25: "Co 30 sekund, Isaac ma szansę na zrobienie kupy lub puszczenie bąka. Bąki zadają 6 Obrażeń i odpychają pociski przeciwników.",
  21: "Co każdą klatkę, ma szansę na wywołanie efektu The Polaroidu, The Negative'u, A Missing Page lub Missing Poster. Może to umożliwić Isaacowi przejście do Chest/Dark Roomu lub bycie odrodzonym jako The Lost.\nMysterious Paper nie może zostać użyty do otwarcia Strange Door.",
  88: "Zapobiega generacji przedmiotów aktywnych z wszystkich pul, jeśli jest to możliwe.\nJeśli Isaac posiada podwojony efekt tego trinketu (np. z Mom's Box), powoduje przelosowanie przedmiotów o jakości 0.\nDziała również na ustalone przedmioty, np. Grzechy Główne będą wyrzucały znajdźki zamiast aktywnych przedmiotów, Krampus zawsze wyrzuci A Lump of Coal, a Headless Horseman i Conquest wyrzucą Cube of Meat lub Ball of Bandages.",
  98: "Daje szansę (niezależną od Szczęścia) na wystrzelenie samonaprowadzającej łzy, który przyczepia się do przeciwników i zadaje im Obrażenia Isaaca co sekundę. Łza utrzymuje się na przeciwniku maksymalnie 10 sekund.",
  165: "Od Rozdziału 4, zastępuje wszystkie monety i klucze następującymi znajdźkami:\n* bomba - 26,6% szansy\n* serce - 26,6% szansy\n* pigułka - 16,6% szansy\n* karta - 16,6% szansy\n* trinket - 6,6% szansy\n* bateria - 6,6% szansy\nMonety mają również 20% szansy na bycie zastąpionymi muszką.\nEfekt nie działa w Greed Modzie ani podczas Ascentu.",
  174: "Zwiększa szansę na Devil Room o 10%, zapobiega pojawieniu się Krampusa w Devil Roomach oraz zmienia wygląd Devil Roomów. Nowe Devil Roomy będą zawierały 0-3 przedmioty z puli Devil Roomu, 1-3 Black Hearty oraz przeciwników z Sheolu.",
  143: "Przedmioty aktywne nie otrzymują ładunków po przejściu pokoju, ale zamiast tego istnieje szansa na wygenerowanie Lil' Battery, niezależnie od innych nagród.",
  96: "+1,5 Zasięgu.\n+0,4 Łez.\n+2 Wysokości Łez.\nDaje Isaacowi łzy spektralne.\nŁzy Isaaca poruszają się w spirali.\nDaje szansę na łzę samonaprowadzającą. Szansa jest zależna od Szczęścia i wynosi 100% przy Szczęściu 9.",
  149: "Aktywuje przedmiot aktywny Isaaca (jeśli jest naładowany) w momencie, gdy Isaac otrzymuje obrażenia. Efekt przedmiotu zadziała **przed** zadaniem Isaacowi obrażeń, co oznacza, że np. aktywowanie Book of Shadows zablokuje obrażenia.\nEfekt nie aktywuje się przy otrzymaniu obrażeń od wybuchów, kolców, Spiked Chestów/Mimic Chestów, ataków nogą Mamy i Szatana, Whipperów i ich wariantów, laserów Brimstone'a, przedmiotów, które zadają Isaacowi obrażenia, Devil Beggarów, Blood Donation Machine oraz Konfesjonałów.",
  19: "Pozwala Isaacowi na otwarcie Golden Chestów bez zużycia klucza.\nTrinket startowy Caina po wpłaceniu 68 monet do Greed Donation Machine.",
  112: "Treasure Roomy będą zawsze zawierały Restock Boxy. Efekt jest aplikowany w momencie generacji piętra, więc Isaac musi trzymać trinket, gdy przechodzi na nowe piętro, żeby efekt zaszedł.\nNie ma wpływu na 2 Treasure Roomy w ???.\nW Greed Modzie efekt działa na oba Treasure Roomy, srebrny i złoty.",
  145: "+10 Szczęścia.\nTrinket ten zostanie zniszczony, gdy Isaac otrzyma obrażenia, nie wliczając obrażeń z Blood Donation Machine/Devil Beggarów/Hell Game/Konfesjonałów, Curse Roomów, Spiked Chestów, pigułek Bad Trip, przedmiotów zadających obrażenia (np. Sharp Plug) oraz drzwi do przejścia do Mausoleum/Gehenny.\nJedynym sposobem na zdobycie tego trinketu jest przejście 3 kolejnych pięter bez otrzymania obrażeń. Trinket zostanie wygenerowany po pokonaniu bossa. Piętra XL liczą się jako jedno piętro.",
  2: "Zwiększa szansę na znajdźki z kup o 50%.",
  30: "Daje Isaacowi szansę na wystrzelenie trującej łzy.\nSzansa wynosi 1/(10 - Szczęście/2).",
  76: "Jeśli skrzynka zawiera serca, monety, bomby i/lub klucze, trinket daje 50% szansy na wyrzucenie dodatkowych znajdziek i 50% szansy na zastąpienie zawartości skrzynki muchą Attack Fly.\nNie ma efektu na Red Chesty i na skrzynki, które zawierają pigułki, karty, trinkety lub przedmioty.",
  167: "Daje szansę na przyzwanie przyjacielskiego Bony'ego lub Black Bony'ego po przejściu pokoju.\nSzansa jest zależna od Szczęścia i wynosi 100% przy Szczęściu 51.",
  9: "Łzy Isaaca będą pulsować, zmieniając rozmiar pomiędzy 1x a 1,6x. Wpływa to na ich hitbox.",
  5: "Podwaja szansę na pojawienie się przeciwników-championów.\nZwiększa szansę na pojawienie się bossów-championów.",
  31: "Daje szansę na wystrzelenie przeszywającej i spektralnej łzy.\nSzansa wynosi 1/(10 - Szczęście/2).",
  64: "Łzy Isaaca otrzymują efekty trinketów-robaków, które zmieniają się co 3 sekundy w następującej kolejności: Pulse Worm, Ring Worm, Tape Worm, Whip Worm, Wiggle Worm, Flat Worm, Hook Worm, Lazy Worm, Ouroboros Worm, Brain Worm.",
  179: "Zamiast podążać w linii za Isaaciem, jego chowańce będą poruszać się tak jak on. Przytrzymanie klawiszu wyrzucenia karty/pigułki (domyślnie Ctrl) pozwala na oddzielenie chowańców od Isaaca.\nNie ma wpływu na chowańce, które naśladują ruchy Isaaca z opóźnieniem.",
  40: "Przy otrzymaniu obrażeń, Isaac ma szansę na otrzymanie bonusu +1,8 do Obrażeń na czas trwania obecnego pokoju.\nSzansa jest zależna od Szczęścia i wynosi 100% przy Szczęściu 8.",
  84: "Zapobiega pojawianiu się Greeda i Super Greeda w Sklepach i Secret Roomach.\nZwiększa szansę na monety w nagrodach po przejściu pokoju, kosztem serc.\nJeśli Rib of Greed zostanie podniesiony po tym, jak Greed/Super Greed już pojawił się w Sklepie lub Secret Roomie, pokoje te zostaną zamienione w ich normalne warianty.",
  164: "Gdy Isaac stawia bombę, postawi za darmo drugą dodatkową bombę.",
  11: "+0,47 Łez.\nDaje Isaacowi łzy spektralne.\nŁzy Isaaca poruszają się w spirali.",
  7: "Zwiększa szansę na to, że Angel Room zastąpi Devil Room z bazowej 50% na 75%.\nZwiększa szansę na napotkanie The Bible w Library na 45%, a w Sklepie na 13%.",
  126: "Generuje Niebieską Muszkę, gdy Isaac podnosi monetę.",
  36: "Daje 10% szansy na zmienienie nagrody po przejściu pokoju na klucz.\nDaje szansę na dodanie klucza do znajdziek ze skrzynek, Tinted Rocków, Slot Machine i Fortune Telling Machine.",
  44: "Daje 10% szansy na zmienienie nagrody po przejściu pokoju na pigułkę.\nDaje szansę na dodanie pigułki do znajdziek ze skrzynek, Tinted Rocków, Slot Machine i Fortune Telling Machine.",
  63: "Zmienia Troll Bomby w znajdźki-bomby przed ich wybuchem. Mega Troll Bomby zmieniają się w podwójne bomby, Złote Troll Bomby w Złote Bomby, a Giga Bomby w znajdźki-Giga Bomby.\nNie ma efektu na bomby Wratha ani płonące bomby Big Horna.",
  58: "Daje szansę na danie Isaacowi bonusu +0,5 do Obrażeń na czas trwania obecnego pokoju za każdą śmierć przeciwnika, maksymalnie 10 bonusów na pokój.\nSzansa jest zależna od Szczęścia i wynosi 100% przy Szczęściu 10.",
  78: "Zwiększa czas trwania efektów statusu przeciwników (strachu, oczarowania, itp.).",
  62: "Tinted Rocki oraz kamienie z Crawl Space pod nimi zaświecą się na biało, gdy Isaac wejdzie do pokoju, a potem co 10 sekund.",
  133: "Bomby postawione przez Isaaca będą wybuchać w 0,66 sekundy, a nie 1,5 sekundy, i będą zadawać 15% więcej obrażeń.",
  189: "Po zabiciu przeciwnika daje Isaacowi tarczę, która chroni go przed obrażeniami, na 1 sekundę. Zabicie kolejnych przeciwników spowoduje dodanie 1 sekundy do czasu trwania tarczy, maksymalnie 10 sekund.\nNie działa na Monety Ultra Greeda, mimo że są one uznawani przez grę za przeciwników.",
  110: "Sklepy będą pojawiać się w Womb/Utero/Scarred Womb/Corpse.\nEfekt jest aplikowany w momencie generacji piętra, więc Isaac musi trzymać trinket, gdy przechodzi na nowe piętro, żeby efekt zaszedł.",
  119: "Przy przejściu na następne piętro leczy Isaaca do 1/2 jego Czerwonych Serc lub, jeśli już ma zdrowie równe lub większe od 1/2 jego Czerwonych Serc, leczy go o 1/2 Czerwone Serca.",
  13: "Jeśli Isaac trzyma ten trinket, wszystkie przedmioty i znajdźki w Sklepie będą przecienione na 0 monet. Po zakupie jednego przedmiotu/znajdźki, trinket znika.\nW przypadku Tainted Keepera nie działa na przedmioty poza Sklepem.",
  83: "Otwieranie Sklepów nie zużywa kluczy.\nTrinket startowy Keepera po pokonaniu przez niego Szatana.",
  175: "Otwiera przejście do ??? po pokonaniu Mom's Heart/It Lives!, niezależnie od czasu.\nUżycie Pandora's Box powoduje wygenerowanie 6 przedmiotów z losowych pul oraz zniszczenie Strange Key, zamiast normalnego efektu.",
  74: "Daje kamieniom szansę na wygenerowanie Crawl Space, kiedy zostaną zniszczone. Szansa jest niezależna od lokacji kamienia.",
  99: "Daje szansę na wystrzelenie łzy z efektem Rubber Cement, odbijającej się od przeszkód i przeciwników.",
  68: "Isaac będzie przyciągać znajdźki i przeciwników do siebie.\nPrzyciągane znajdźki mogą przejść przez kamienie i przepaści, ale nie Key Blocki.\nIsaac będzie przyciągać serca tylko, jeśli będzie mógł je podnieść z obecnym stanem zdrowia.",
  178: "Przy otrzymaniu obrażeń Isaac ma 50% szansy na wywołanie wybuchu, który zadaje 185 Obrażeń i synergizuje się z jego efektami bomb.\nWejście do Curse Roomu i wejście na kolce w Sacrifice Roomie nie wywoła wybucha.\nOddanie serc do Konfesjonału lub Blood Donation Machine i wywołanie eksplozji nie niszczy ich, ale powoduje, że wyrzucają dodatkowe znajdźki, tak jakby zostały zniszczone.",
  1: "Generuje 1 monetę przy otrzymaniu obrażeń przez Isaaca.\nW przypadku Keepera i Tainted Keepera generuje 0-1 monet.\nMa szansę wypaść z dzbanów i Special Shopkeeperów.",
  65: "+3 Zasięgu.\n0,5x Wysokości Łez.",
  139: "Efekty łez zależne od Szczęścia mają większą szansę na wywołanie (równowarte bonusowi +3 Szczęścia).",
  152: "Zwiększa szansę na wygenerowanie Planetarium o 9%.\nPo wejściu do pierwszego Planetarium, utrzymuje szansę na wygenerowanie następnego na 15%.\nUmożliwia wygenerowanie Planetariów w Womb/Utero/Scarred Womb/Corpse.",
  177: "Generuje skrzynkę po przejściu Challenge Roomu.\nGeneruje dodatkowy przedmiot z puli Boss Roomu po przejściu Boss Challenge Roomu.",
  61: "Zamienia wszystkie nowe skrzynki w Red Chesty, oprócz Old Chestów i Mega Chestów.\nSkrzynki w pokoju startowym Chestu zostaną zamienione w Red Chesty zawierające układy z Diabłem, jak skrzynki w Dark Roomie.",
  183: "Daje 50% szansy na skopiowanie jednego z chowańców Isaaca przy wejściu do pokoju.\nJeśli Isaac nie ma żadnych chowańców, zamiast tego generuje Brother Bobby'ego lub Sister Maggy.\nMa małą szansę wypaść z Horny Boys.",
  53: "Usuwa 15% zdrowia każdego przeciwnika, który ma powyżej 60 Punktów Zdrowia.\nLeczy Isaaca o 1 Czerwone Serce, gdy wchodzi on do nowego Boss Roomu.\nTick nie może zostać wyrzucony ani zastąpiony przez inny trinket, z wyjątkiem Match Stick oraz A Lighter.",
  97: "Po otrzymaniu 6-12 obrażeń przez Isaaca, generuje chowańca, który podąża za Isaaciem i blokuje pociski przeciwników. Efekt może zajść 2 razy, po czym trinket znika.",
  157: "Co 15 strzałów, Isaac wystrzeli łzę z efektem Ipecaca i My Reflection i dużym zasięgiem.",
  158: "Przy otrzymaniu obrażeń, Isaac wyrzuca 2 losowe znajdźki ze swojego ekwipunku (oprócz serc).\nZnajdźki podlegają normalnym zasadom generacji znajdziek, tzn. moneta może zostać zastąpiona Nickelem, Dime'em, itp., bomba Troll Bombą, Złotą Bombą, itp.",
  33: "Gdy Isaac wchodzi do pokoju i ma 1/2 Czerwonego Serca lub mniej, wygenerowany zostanie chowaniec podobny do Little Stevena, który pozostanie z Isaaciem, dopóki nie będzie on miał zdrowia większego niż 1/2 Czerwonego Serca. Isaac może w ten sposób uzyskać tylko jednego takiego chowańca.\nGdy Isaac otrzymuje obrażenia, istnieje szansą na wygenerowanie chowańca podobnego do Gemini na czas trwania obecnego pokoju. Efekt ten może zajść więcej niż jeden raz.",
  93: "Daje efekt Skatole w niektórych pokojach.",
  100: "Daje Isaacowi +0,25 Szybkości, +0,75 Zasięgu, +0,2 Łez, +0,1 Szybkości Strzałów, +0,5 Obrażeń i +1 Szczęścia, gdy jego przedmiot aktywny jest w całkowicie naładowany.\nEfekt nie działa na przedmioty aktywne bez ładunków ani gdy Isaac nie posiada przedmiotu aktywnego.\nNie ma efektu na przedmioty w miejscu na kartę/pigułkę.\nW przypadku Bethany i Tainted Bethany, efekt działa również wtedy, gdy ich przedmioty są naładowane ładunkami Soul Heartów/Czerwonych Serc.",
  108: "Po tym, jak Isaac zostanie uderzony przez 1-9 wybuchów, trinket zostanie zniszczony i wyrzuci monetę, serce, klucz i inny trinket.",
  72: "Zwiększa szansę na wygenerowanie Lil' Battery po przejściu pokoju o 6,67%.\nZwiększa szansę na to, że losowa znajdźka będzie Lil' Battery, o 2%.\nMa 5% szansy na dodanie ładunku do przedmiotu aktywnego Isaaca po przejściu pokoju.",
  27: "+0,5 Szybkości Strzałów.",
  161: "Powoduje, że Treasure Room i Sklep zostaną wygenerowane w Sheolu.",
  10: "+0,4 Łez.\nDaje Isaacowi łzy spektralne.\nŁzy Isaaca poruszają się falowo.",
  104: "Gdy Isaac otrzyma obrażenia, istnieje 5% szansy na zniszczenie trinketu i wygenerowanie losowego przedmiotu z puli obecnego pokoju.",
  121: "Daje efekt Holy Mantle na początku każdego piętra.\nEfekt dodaje się z efektem Holy Mantle i Blanketu (ale nie Holy Card) i ma z nich najwyższy priorytet.",
  173: "Trinket może zostać użyty do zapłaty za układ z Diabłem/Black Market bez zużycia serc. Po użyciu trinket znika.\nNie może zostać użyty do zakupu układu z Diabłem za pieniądze, np. jako Keeper lub z A Pound of Flesh.\nUżycie trinketu liczy się jako wzięcie układu z Diabłem do szansy na Angel Room.",
  102: "Powoduje, że na nowych piętrach zostaną wygenerowane 2 Secret Roomy.\nNie ma efektu w Greed Modzie.",
  111: "Powoduje, że na piętrach Womb/Utero/Scarred Womb/Corpse zostaną wygenerowane Treasure Roomy.",
  82: "Dodaje 15% szansy na to, że na kolejnych piętrach wygenerowane zostaną Treasure Roomy z 2 przedmiotami. Można wziąć tylko jeden z tych przedmiotów, drugi zniknie.",
  118: "Gdy Isaac zabija przeciwnika lub niszczy Shopkeepera, ma szansę na otrzymanie umiejętności lotu na czas trwania pokoju.",
  122: "Gdy Isaac użyje przedmiotu aktywnego, zostanie on wyrzucony na piedestał na ziemi.\nGdy Isaac otrzymuje obrażenia, istnieje 2% szansy na to, że jeden z jego przedmiotów pasywnych zostanie wyrzucony na piedestał na ziemi.\nW przypadku przedmiotów aktywnych w miejscu na pigułkę, są one traktowane jak przedmioty pasywne. Po wyrzuceniu na piedestał i ponownym podniesieniu zajmą one normalne miejsce przedmiotu aktywnego, a miejsce w miejscu na pigułkę zostanie stracone.",
  94: "Przedmioty i trinkety, które generują Niebieskie Muszki lub Szarańcze, będą ich generować 2 razy więcej.",
  95: "Daje Isaacowi szansę na wystrzelenie zęba, który zadaje 2x jego Obrażeń oraz zatruwa przeciwników.\nSzansa jest zależna od statystyki Szczęścia i wynosi 100% przy Szczęściu 32.",
};

