  41: "Zadaje 40 Obrażeń wszystkim przeciwnikom w pokoju, niszczy wszystkie znajdźki i zamienia je w Niebieskie Muchy i Pająki oraz niszczy wszystkie przedmioty w pokoju i zamienia je w losowe bonusy do statystyk.",
  42: "Przy użyciu, Isaac rzuca kartą w kierunku, w którym się porusza. Karta zabija wszystko na swojej drodze, wliczając bossów i normalnie nietykalnych przeciwników, takich jak Stone Grimace; jedynymi wyjątkami są Delirium, The Beast i Mother's Shadow.\nZabija tylko jedną fazę Mega Szatana, Husha, Ultra Greediera, Matki oraz Dogmy.\nZabicie przy jej pomocą Great Gideona otworzy na jego miejscu przejście do specjalnego Crawl Space'u, który zawiera skrzynki oraz przedmioty.\nOprócz tego karta jest w stanie otwierać drzwi otwierane przy pomocy eksplozji, niszczyć kamienie oraz gasić ogniska.",
  43: '"Kupuje" za darmo wszystkie przedmioty w Sklepie, Devil Roomie lub Black Markecie.\nWzięcie tak zakupionych przedmiotów z Devil Rooma/Black Marketu nie wpływa na szansę na Angel Room.',
  44: "Wyświetla tajemniczą wiadomość, która zawiera wskazówki dotyczące gry.",
  45: "Wypełnia pokój kupami.",
  46: "Zabija Isaaca i generuje 20 znajdziek, przedmiotów i/lub skrzynek. Przedmioty będą z puli obecnego pokoju.",
  47: "Otwiera wszystkie drzwi w obecnym pokoju, wliczając drzwi do Mega Szatana oraz wyjście z Boss Rusha od wewnątrz, ale nie wliczając wejścia do Boss Rusha, wejścia do Husha, Strange Door ani drzwi do Serca Matki w Mausoleum/Gehennie II.",
  48: "Wywołuje efekt przedmiotu aktywnego Isaaca.\nNie działa na przedmioty w miejscu na pigułkę/kartę.",
  49: "Wywołuje efekt The D6 i D20.",
  50: "Przy użyciu dwie ręce Mamy zaatakują 2 losowych przeciwników, zadając im obrażenia i przytrzymując ich bez ruchu.",
  51: "Daje jednorazowy efekt Holy Mantle, chroniąc przed jednym przyjęciem obrażeń.\nTainted Lost zaczyna z Holy Card, a karty te są znacznie częstsze niż normalnie i można je spotkać nawet jeśli nie zostały odblokowane.",
  52: "Daje Isaacowi +7 Obrażeń, +3 Zasięgu oraz możliwość niszczenia przeszkód przez wchodzenie w nie na czas trwania obecnego pokoju.",
  53: "Generuje 3 karty (wliczając Cracked Key, Dice Shard i Emergency Contact, ale nie runy).",
  54: "Na czas trwania obecnego pokoju spowalnia wszystkich przeciwników, zwiększa Szybkość Isaaca o +0,5 oraz zmniejsza jego Szybkość Strzałów o -1.",
  55: "Aktywuje osłabiony efekt losowej runy:\n* Algiz - daje tarczę na 3 sekundy\n* Ansuz - ujawnia mapę do 3 pokoi od obecnego\n* Berkano - generuje 1 Niebieską Muszkę lub Pająka\n* Dagaz - daje Isaacowi 1/2 Soul Hearta\n* Ehwaz - generuje zapadnię\n* Hagalaz - niszczy niektóre kamienie w pokoju\n* Jera - podwaja jedną znajdźkę w pokoju\nPerthro - przelosowuje jeden przedmiot w pokoju\nRune Shard pojawia się tylko jako zastępstwo run, które jeszcze nie zostały odblokowane. W momencie, gdy 6 run (wliczając Soul Stone'y) zostanie odblokowane, Rune Shardy przestaną się generować.",
  56: "Wyrzuca wszystkie serca i znajdźki, jakie Isaac posiada, zostawiając mu 1/2 serca.\nZnajdźki mogą być wyrzucone jako przedmioty takie jak A Quarter albo Boom!.",
  57: "Daje Isaacowi efekt podobny do The Soul (ale silniejszy i z większym polem działania) na 1 minutę.",
  58: "Przez 1 minutę nogi Mamy będą próbować uderzyć Isaaca, podobnie do efektu Broken Shovel.",
  59: "Daje Isaacowi tymczasowe 2 kontenery czerwonych serc (lub odpowiednie serca dla postaci, które nie mogą posiadać czerwonych serc) oraz +1,35 Łez na 1 minutę.\nPo upływie czasu kontenery serc znikają, chyba że zabiłoby Isaaca, wtedy zostaje mu 1 kontener.",
  60: "Teleportuje Isaaca do dodatkowego Boss Roomu, którego boss jest z puli piętra o 2 piętra dalszego niż obecne. Boss wyrzuca przedmiot, ale nie generowane jest przejście do następnego piętra.\nWyjście z Boss Roomu przeniesie Isaaca do pokoju, w którym użył karty.\nUżycie w Womb/Utero/Scarred Womb/Corpse wygeneruje walkę z bossem z puli obecnego piętra.\nUżycie w Sheolu/Cathedral/Dark Roomie/Cheście/The Void/Home teleportuje Isaaca do losowego pokoju.\nUżycie w ??? wygeneruje walkę z bossem z puli bossów Womb.",
  61: "Generuje 2 Bone Hearty.",
  62: "Generuje przedmiot z puli obecnego pokoju, w zamian zabierając 1 kontener czerwonego serca lub 2 Soul Hearty (potencjalnie zabijając Isaaca) i dając Isaacowi Złamane Serce.\nKeeper oraz Tainted Keeper tracą jedną monetę zdrowia i zyskują Złamaną Monetę.\nPostacie, które nie mają zdrowia, nie tracą nic.",
  63: "Isaac na 10 sekund staje się nietykalnym posągiem, który może się lekko poruszać i ma dużą statystykę łez.",
  64: "Generuje 2-4 Golden Chesty.",
  65: "“Sprzedaje” wszystkie przedmioty i znajdźki w obecnym pokoju za ich normalną cenę w Sklepie, generując odpowiednią liczbę monet.\nJeśli nie ma nic do sprzedania, generuje 1 centa.",
  66: "Wywołuje efekt losowego Dice Roomu.",
  67: "Wszyscy przeciwnicy w pokoju otrzymują efekt spowolnienia oraz będą przyjmować podwójne obrażenia. Efekt trwa maksymalnie 1 minutę.",
  68: "Na 30 sekund Isaac otrzymuje potrójny strzał i -0,3 Szybkości. Zabici przeciwnicy wyrzucają losowe monety.",
  69: "Wywołuje efekt Book of the Dead (generuje kościanego orbitala lub przyjaznego Bony'ego/Bone Fly/Black Boney'ego/Revenanta za każdego zabitego w pokoju przeciwnika).",
  70: "Isaac użyje 5 losowych pigułek.",
  71: "Na 30 sekund wywołuje efekt The Bible (może zabić Mamę) i daje Isaacowi chowańca Seraphima.",
  72: "Generuje 6 grup losowych kamieni lub innych przeszkód.\nNigdy nie blokuje wyjść z pokoju.",
  73: "Usuwa najstarszy pasywny przedmiot Isaaca (pomijając przedmioty startowe) i generuje 2 przedmioty z puli obecnego pokoju.\nNie robi nic, jeśli Isaac nie ma przedmiotów.",
  74: "Teleportuje Isaaca do Ultra Secret Roomu.",
  75: "Daje Isaacowi latanie oraz +1,5 Obrażeń, zamienia jego czerwone serca na Bone Hearty oraz wywołuje Curse of Darkness, której nie można się pozbyć, na czas trwania obecnego piętra.\nPo zakończeniu efektu serca ponownie stają się czerwonymi sercami.",
  76: "Generuje Restock Machine.",
  77: "Generuje zapadnię, która prowadzi do Crawl Space.",
  78: "Generuje Red Room, podobnie jak Red Key.",
