  80: "Wywołuje efekt ostatnio użytej pigułki, karty, runy, Soul Stone'a lub przedmiotu aktywnego.",
  81: "Wszystkie przedmioty w pokoju będą zmieniały się między oryginalnym przedmiotem a innym, podobnie do efektu Glitched Crown.\nPonowne użycie tego Soul Stone'a doda nowy przedmiot do obiegu.",
  82: "Wszyscy przeciwnicy w obecnym pokoju po zabiciu wyrzucą połówki czerwonych serc, które znikną po 2 sekundach.",
  83: "Otwiera wszystkie drzwi w pokoju, wliczając przejścia do Red Roomów.\nNiszczy lustro w Downpour/Dross II.",
  84: "Zatrzymuje czas na 3 sekundy i wywołuje efekt Dark Arts.",
  85: "Przy użyciu Isaac puści 8 trujących bąków, wytworzy brązowy śluz (stanie na którym daje mu +1,35 Łez i +1 Obrażeń), po czym wygeneruje 7 zapalonych Butt Bomb.",
  86: "14 chowańców podobnych do Dead Birda przyleci do pokoju i będzie atakować przeciwników dopóki wszyscy nie zginą lub Isaac nie wyjdzie z pokoju.",
  87: "Wywołuje efekt Berserk! na 10 sekund, dając Isaacowi +0,4 Szybkości, +3 Obrażeń, bonus do Łez oraz atak ręczny.",
  88: "Wywołuje efekt Mega Blast na 7,5 sekundy.",
  89: "Przy normalnych użyciu zabija Isaaca i natychmiast go wskrzesza z 1/2 serca w tym samym miejscu. Soul Stone ten aktywuje się również automatycznie, gdy Isaac zginie.\nMa największy priorytet ze wszystkich odradzających Isaaca przedmiotów/trinketów/znajdziek.",
